.weather-box {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.img {
  width: 30%;
  /* height: 6rem;
  width: 6rem; */
  margin-top: 2rem;
}

.temp-container {
  margin: 1rem auto 0;
  background: rgba(55, 55, 55, 0.5);
  border-radius: 16px;
  width: 90%;

  box-shadow: inset 3px 6px 0px 2px rgba(50, 50, 50, 0.5),
    inset 3px 6px 6px 4px rgba(50, 50, 50, 0.3);
}

.temp {
  filter: blur(0);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem auto 0;
  width: 90vw;
  padding: 0 1.5rem;
  color: #fff;
  font-size: 5rem;
  font-weight: bolder;
  text-align: center;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
}

.degree {
  font-size: 2rem;
  padding-bottom: 3rem;
}

.feels-like-container {
  font-size: 1rem;
  color: #fff;
  margin-bottom: 1rem;
  letter-spacing: 1px;
  font-style: italic;
}

.weather {
  color: #fff;
  font-size: 2.5rem;
  font-weight: bolder;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.misc-weather {
  font-size: 1.5rem;
  line-height: 1;
  color: #fff;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-self: flex-end;
  justify-self: flex-start;
  align-items: center;
  width: 50%;
  padding-left: 0;
}

.wind-container {
  margin-bottom: 2rem;
}

.wind-icon {
  width: 2rem;
  height: 2rem;
  margin-top: 0.5rem;
}

.weather-description {
  width: 70%;
}
