.search-box {
  width: 90%;
  position: absolute;
  top: 2rem;
}

.search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  box-shadow: 0px 5px 3px rgba(0, 0, 0, 0.2), 0px 10px 5px rgba(0, 0, 0, 0.1);
  color: #313131;
  font-size: 1rem;

  transition: 0.4s ease;
}

.search-bar:focus {
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: none;
  border-radius: 0;
  transition: 0.4s ease;
  transform: scale(1.05);
}
