.date {
  display: block;
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  line-height: 1.1;
  color: #fff;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.85);
  margin: 0.25rem auto;
  letter-spacing: 2px;
  background-color: rgba(50, 50, 70, 0.5);
  padding: 10px;
}

.day {
  font-weight: bolder;
}
