* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* border: 1px solid red; */
}

body {
  font-family: "Oswald", sans-serif;
}

.app {
  background-image: url(./assets/warm-bg.jpg);
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease-out;
  height: 100vh;
  width: 100vw;
}

.clear {
  background-image: url(./assets/clear1.jpg);
  background-position: right;
}

.warm {
  background-image: url(./assets/warm-bg.jpg);
}

.clouds {
  background-image: url(./assets/clouds1.jpg);
}

.thunderstorm {
  background-image: url(./assets/tstorm2.jpg);
}

.drizzle {
  background-image: url(./assets/drizzle2.jpg);
}

.rain {
  background-image: url(./assets/rain2.jpg);
}

.snowy {
  background-image: url(./assets/snow2.jpg);
  background-position: left;
}

.fog {
  background-image: url(./assets/fog1.jpg);
  background-position: bottom;
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0.35)
  );
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.results {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 2rem;
}

.default-screen {
  font-size: 1.5rem;
  width: 90%;
  text-align: center;
  margin-top: 5rem;
  line-height: 1;
  color: #fff;
  width: 100%;
  letter-spacing: 1px;
  position: relative;
  bottom: 8rem;
  padding: 1rem;
  background: rgba(48, 48, 54, 0.35);
}

.default-screen p {
  margin-top: 1rem;
}
